body, .app {
	overflow: hidden;
}

.app__main {	
	margin-top: 30px;
	height: calc(100vh - 30px);
	overflow: auto;
}

.app__header {
	color: #cfe6d1;
	height: 30px;
	width: 100%;
	background: #1b2d13;
	position: fixed;
	top: 0;
	z-index: 1;
}

.app__title {
	position: relative;
	top: 3px;
	padding: 0 10vw 0 10vw; 
	font-weight: bold;
}